export class UserModel {
    UserId: string
    Username: string
    DisplayName: string
    Status: string
    LockDate: string
    RoleId: string
    RoleName: string
    Email: string
    Avatar: string
    Birthday: string
    Password: string
    PasswordConfirm: string
    PhoneNumber: string
    constructor(username: string, password: string, displayname: string, roleID: string, email: string, birthday: string, phoneNumber: string) {
        this.Username = username
        this.Password = password
        this.DisplayName = displayname
        this.RoleId = roleID
        this.Email = email
        this.Birthday = birthday
        this.PhoneNumber = phoneNumber
    }
}